* {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  font-family: arial;
}

h1 {
  margin: 30px 0;
}

section {
  margin: 50px 0;
  display: flex;
  justify-content: center;
}

button {
  width: 150px;
  padding: 10px 20px;
  background-color: white;
  color: black;
  border: 2px solid black;
  font-size: 20px;
  cursor: pointer;
  transition: 0.2s;
  margin-right: 5px;
}


button:nth-of-type(5) {
  margin-right: 0;
}

button:hover {
  background-color: black;
  color: white;
}

h3 {
  margin: 20px 0;
  font-size: 26px;
}

h4 {
  font-size: 26px;
  font-weight: bold;
  color: blue;
}

img {
  width: 50px;
  height: 50px;
}

@media(orientation:landscape) and (max-width:534px) {
  button {
    width: 120px;
    font-size: 14px;
    padding: 7px 14px;
  }

}

@media (orientation:portrait) {

  section {
    flex-direction: column;
    align-items: center;
  }

  button {
    margin-bottom: 2px
  }

  button:nth-of-type(5) {
    margin-right: 5px;
  }
}